import classNames from 'classnames';
import React from 'react';
import { iconButtonStyles } from '../../styles/common-styles';

export interface IconLinkProps extends React.AnchorHTMLAttributes<HTMLAnchorElement> {
  readonly label: string;
}

export const IconLink: React.FC<IconLinkProps> = React.forwardRef<HTMLAnchorElement, IconLinkProps>(
  ({ label, className, children, ...props }, ref) => (
    <a className={classNames(iconButtonStyles, className)} ref={ref} {...props}>
      {children}
      <span className="visually-hidden">{label}</span>
    </a>
  )
);

IconLink.displayName = 'IconLink';
